<template>
    <v-container fluid fill-height style="padding:0;" id="grid" grid-list-md>
        <Blog :items="items" :time="time">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Explain">解释</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class=".body-1">1. <code>grid-template-columns</code>用来设置网格列，<code>grid-template-columns: 1fr 1fr 1fr;</code>表示创建3个等宽列，1fr表示占比的意思</p>
                                    <p class=".body-1">2. <code>grid-template-rows: repeat(3, 1fr);</code>设置网格行，表示重复3个等高行</p>
                                    <p class=".body-1">3. <code>grid-row-gap</code>设置行距</p>
                                    <p class=".body-1">4. <code>grid-auto-rows: minmax(100px, auto);</code>表示自动创建的行高将会是最小100px，最大为auto</p>
                                    <p class=".body-1">5. <code>grid-area: 2(grid-row-start) / 1(grid-column-start) / 3(grid-row-end) / 3(grid-column-end);</code>设置网格位置和跨度</p>
                                    <p class=".body-1">6. <code>grid-column: 3 / span 4;</code>设置网格从第三列开始跨度为四列</p>
                                    <p class=".body-1">7. <code>grid-row: 2;</code>设置网格从第二行开始跨度为一行</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <div class="mygrid">
                                        <div class="header"><span>头部</span></div>     
                                        <div class="content">
                                            <div class="menu">菜单</div>
                                            <div class="main">
                                                <div>内容一</div>
                                                <div>内容二</div>
                                                <div>
                                                    <div>1</div>
                                                    <div>2</div>
                                                    <div>3</div>
                                                    <div>4</div>
                                                    <div>5</div>
                                                    <div>6</div>
                                                    <div class="span2">7</div>
                                                    <div class="span4">8</div>
                                                </div>
                                            </div>
                                        </div>    
                                    </div>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Code1">代码</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                    <v-tab key="html" ripple>HTML</v-tab>
                                    <v-tab key="css" ripple>CSS</v-tab>
                                    <v-tab-item key="html">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{html}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                    <v-tab-item key="css">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">{{css}}</pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                </v-tabs>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-14",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            <div class="mygrid">
                <div class="header"><span>头部</span></div>     
                <div class="content">
                    <div class="menu">菜单</div>
                    <div class="main">
                        <div>内容一</div>
                        <div>内容二</div>
                        <div>
                            <div>1</div>
                            <div>2</div>
                            <div>3</div>
                            <div>4</div>
                            <div>5</div>
                            <div>6</div>
                            <div class="span2">7</div>
                            <div class="span4">8</div>
                        </div>
                    </div>
                </div>    
            </div>
            `,
            css: `
            .mygrid{
                height: 500px;
                display: grid;
                grid-template-rows: 50px 1fr;
            }
            .mygrid .header {
                background-color:azure;
                display: grid;
                align-items: center;
                justify-content: center;
            }
            .mygrid .content {
                display: grid;
                grid-template-columns: 200px 1fr;
                grid-auto-rows: minmax(100px, auto);
            }
            .mygrid .content .menu {
                display: grid;
                align-items: center;
                justify-content: center;
                background-color:darkgray;
            }
            .mygrid .content .main {
                display: grid;
                grid-template-rows: repeat(3, 1fr);
                grid-row-gap: 5px;
            }
            .mygrid .content .main > div {
                display: grid;
                align-items: center;
                justify-content: center;
                background-color: blanchedalmond;
            }
            .mygrid .content .main > div:last-child {
                grid-template-columns: repeat(6, 1fr);
                grid-gap: 20px;
            }
            .mygrid .content .main > div:last-child > div {
                background:chocolate;
            }
            .mygrid .content .main > div:last-child > .span2 {
                grid-area: 2 / 1 / 3 / 3;
            }
            .mygrid .content .main > div:last-child > .span4 {
                grid-column: 3 / span 4;
                grid-row: 2;
            }
            `
        };
    },

    mounted() {}
};
</script>

<style lang="scss">
#grid {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
    }
    .mygrid{
        height: 500px;
        display: grid;
        grid-template-rows: 50px 1fr;
    }
    .mygrid .header {
        background-color:azure;
        display: grid;
        align-items: center;
        justify-content: center;
    }
    .mygrid .content {
        display: grid;
        grid-template-columns: 200px 1fr;
        grid-auto-rows: minmax(100px, auto);
    }
    .mygrid .content .menu {
        display: grid;
        align-items: center;
        justify-content: center;
        background-color:darkgray;
    }
    .mygrid .content .main {
        display: grid;
        grid-template-rows: repeat(3, 1fr);
        grid-row-gap: 5px;
    }
    .mygrid .content .main > div {
        display: grid;
        align-items: center;
        justify-content: center;
        background-color: blanchedalmond;
    }
    .mygrid .content .main > div:last-child {
        grid-template-columns: repeat(6, 1fr);
        grid-gap: 20px;
    }
    .mygrid .content .main > div:last-child > div {
        background:chocolate;
    }
    .mygrid .content .main > div:last-child > .span2 {
        grid-area: 2 / 1 / 3 / 3;
    }
    .mygrid .content .main > div:last-child > .span4 {
        grid-column: 3 / span 4;
        grid-row: 2;
    }
}
</style>

